<script>
var TimeRange = [];
var dateType = "DD";
import MultiSelect from "primevue/multiselect";
import VueApexCharts from "vue-apexcharts";
import DataService from "../../services/DataService";
import ProgressBar from "vuejs-progress-bar";
import Treeselect from '@riophae/vue-treeselect'
// import moment from 'moment';
import TimetrackDashBoardVue from '../TimeTracking/TimetrackDashBoard.vue'
import TimerLoader from '../../components/Common/TimerProductiveLoader.vue'
// import the styles
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
// import { storeToRefs } from "pinia";
// import { useloggedUserInfo } from "@/stores/UserInfo";
// const { GetUserInfo } = storeToRefs(useloggedUserInfo());
export default {
  components: { VueApexCharts, MultiSelect, ProgressBar, Treeselect, TimetrackDashBoardVue,TimerLoader },
  data() {
    return {
      // define options
      // username:'',
      value: [],
      checkedUserIds: [],
      multiselectoptions: [],
      departmentIds: null,
      User: "2",
      ddlUser: [
        {
          id: "1",
          Name: "My View",
        },
        {
          id: "2",
          Name: "Department View",
        },
      ],
      selecteddepartmets: "",
      //Start Productive bar line bar graph
      countries: [],
      options: {
        colors: ["#3c8028","#FFA500","#84857f","#ededed" ],
        chart: {
          type: 'bar',
          stacked: true,
          toolbar: {
            show: false
          },
          zoom: {
            enabled: true
          }
        },
        responsive: [{
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom',
              offsetX: -10,
              offsetY: 0
            }
          }
        }],
        plotOptions: {
          bar: {
            horizontal: false,
            columnWidth: "100%",
          }
        },
        dataLabels: { enabled: false },
        stroke: {
          width: 1,
          colors: ["#fff"]
        },
        title: {
          text: "Productivity Bar"
        },
        xaxis: {
          categories: [],
          labels: {
            formatter: function (value) {
              if (dateType == "DD") {
                for (var item in TimeRange) {
                  if (TimeRange[item].length == 7)
                    TimeRange[item] = "0" + TimeRange[item];
                  if (value == TimeRange[item]){
                    value =value.replace(':00','');
                    if(value[0]=='0'){
                      value=value.substring(1);
                    }
                    return value;
                  }
                }
              } 
              else if (dateType == "WK") {

                if (value == 'Monday 00:00:00' || value == 'Tuesday 00:00:00'|| value == 'Wednesday 00:00:00'|| value == 'Thursday 00:00:00'|| value == 'Friday 00:00:00'|| value == 'Saturday 00:00:00'|| value == 'Sunday 00:00:00')
                {
                  value =value.replace(/ 00:00:00/,'');
                }
                else
                {
                  value="";
                }
                return value;
                 }
              
              else {
                return value;
              }
            },
          },
        },
        
        tooltip: {
          enabled: true,
          y: {
            formatter: function (value) {
              if (value === undefined) {
                return ''; // Return empty string to hide the tooltip
              }
              else if( dateType == "DD") {
              // Customize the tooltip content here
              return value;
              }
              else if( dateType == "Wk") {
              // Customize the tooltip content here
              return value;
              }
              else if( dateType == "MM") {
              // Customize the tooltip content here
              return 'Arrival Time:'+ value 
              + '</br>Left Time:'
              + '</br>Idle Time:'
              + '</br>Productivity(%):' ;
              }
              
             
            },
            title: {
            formatter: function (seriesName) {
              if( dateType == "DD") {
                return seriesName
              }
              if( dateType == "WK") {
                return seriesName
              }
              if( dateType == "MM") {
                return ''
              }
            }
           }
          },
        },
        legend: { position: "top", horizontalAlign: "right", offsetX: 40 },
      },
      series: [
        { name: "Productive App", data: [] },
        { name: "Nuetral App", data: [] },
        { name: "Unproductive App", data: [] },
        { name: "No Record", data: [] },
      ],
      //colors: ["#3c8028","#FFA500","#84857f" ],
      fill: { type: "solid" },
      xaxis: { type: "" },
      //End Productive bar line bar graph
      //Start horizontal chart for Most Used apps
      chartOptions: {
        colors: ["#3c8028","#84857f","#FFA500" ],
        chart: {
          type: "bar",
