          if( VueObj.isChildNodeEnable== true ){
          VueObj.isParentNodeEnable = true;
        }
      }
        VueObj.GetAllAPiMethods();
      }
      else{
        array.forEach(item => {
          VueObj.childerId.push(item.id);
        })
        VueObj.childerId.push(node.id);
        let checkvalueID = VueObj.childerId.findIndex(item=> item == node.id );
          if (checkvalueID > -1) {
          VueObj.childerId.splice(checkvalueID, 1);
        console.log('Cherck value',VueObj.childerId );
        }
        VueObj.selectdepartment=false;
        VueObj.isLoading = false;
        VueObj.deparment= true;
        var arr = "";
        arr=node.label;
        let checkvalue = VueObj.departmentnames.findIndex( item => item == arr);
        if (checkvalue > -1) {
          VueObj.departmentnames.splice(checkvalue, 1);
        }
        else{
          VueObj.departmentnames.push(arr);
          VueObj.departmentnames.join(',');
        }
        VueObj.isParentNodeEnable = true;
        if( VueObj.isParentNodeEnable== true){
