collapse(data){
      var vueObj = this;
      if(data == "unproductive"){
        vueObj.TempUnProductiveApps=[];
        if(vueObj.UnProductiveApps){
          vueObj.UnProductiveApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempUnProductiveApps.push(item);
            }
          });
        }
       vueObj.isShowViewAllUnProductive = true;
      }
      else if(data == "productive"){
        vueObj.TempProductiveApps = [];
        if(vueObj.ProductiveApps){
          vueObj.ProductiveApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveApps.push(item);
            }
          });
        }
        vueObj.isShowViewAllProductive = true;
      }
      else if(data == "ProductiveEmpoyees"){
        vueObj.TempProductiveEmpoyees = [];
        if(vueObj.ProductiveEmpoyees){
          vueObj.ProductiveEmpoyees.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveEmpoyees.push(item);
            }
          });
        }
        vueObj.isShowViewAllEmp = true;
      }
      else if(data == "UnProductiveEployees"){
        vueObj.TempProductiveUnemp = [];
        if(vueObj.UnProductiveEployees){
          vueObj.UnProductiveEployees.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveUnemp.push(item);
            }
          });
        }
        vueObj.isShowViewAllUnEmp = true;
      }
      else{
        vueObj.TempNeutralApps = [];
        if(vueObj.NeutralApps){
          vueObj.NeutralApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempNeutralApps.push(item);
            }
          });
        }

        vueObj.isShowViewAllNeutral = true;
      }
    },
    GetProductivityEmployees: async function () {
      
      var vueObj = this;
      vueObj.isMostProductiveLoading=true;
      vueObj.isMostUnProductiveLoading=true;
      var param = "";
      if (vueObj.dateType == "DD") {
        param = `fromDate=${vueObj.fromDate}&toDate=${vueObj.ToDate}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&type=${vueObj.dateType}&viewType=DepartmentView`;
      } else if (vueObj.dateType == "WK") {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.startDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.endDate)}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&type=${vueObj.dateType}&viewType=DepartmentView`;
      } else {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.monthStartDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.monthEndDate)}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&type=${vueObj.dateType}&viewType=DepartmentView`;
      }
      await DataService.GetProductiveEmployees(param).then((response) => {
        
        vueObj.ProductiveEmpoyees = response.data.productiveEmp;
        vueObj.UnProductiveEployees = response.data.unproductiveEmp;
        // vueObj.$emit('TimetrackDashBoard')
        if ( vueObj.ProductiveEmpoyees != null) {
          vueObj.ProductiveLoader= false;
          vueObj.ProductiveEmpoyees.forEach((item, index) => { 	
            var colors ="";
            if (index==0)
                colors="#e4922a";
            else if (index==1)
                colors="#2c7be5";
            else if (index==2)
                colors="#c749a3";
            else if (index==3)
                colors="#2cb8e5";
            else if (index==4)
                colors="#e5582c";
            else
            colors="#2dbd2d";
            var objProgressOption = {
              text: {
                color: "#FFFFFF",
                shadowEnable: true,
                shadowColor: "#000000",
                fontSize: 12,
                fontFamily: "Helvetica",
                dynamicPosition: false,
                hideText: true,
              },
              progress: {
                color: colors,
                backgroundColor: "#F2F2F2",
                inverted: false,
              },
              layout: {
                height: 8,
                width: 100,
                verticalTextAlign: 61,
                horizontalTextAlign: 43,
                zeroOffset: 0,
                strokeWidth: 30,
                progressPadding: 0,
                type: "line",
              },
            };
            vueObj.ProductiveEmpoyees[index].progress_bar_options = objProgressOption;
            vueObj.isLoading= false;

          });
          vueObj.TempProductiveApps = [];
          if(vueObj.ProductiveApps ){
            vueObj.ProductiveApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveApps.push(item);
            }
          });
          }
          vueObj.TempProductiveEmpoyees=[];
          if (vueObj.ProductiveEmpoyees){
            vueObj.ProductiveEmpoyees.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveEmpoyees.push(item);
            }
          });
          }
          vueObj.isMostProductiveLoading=false;
        }
        else {
          vueObj.isMostProductiveLoading=false;
        }
        if ( vueObj.UnProductiveEployees != null) {
          vueObj.UnProductiveApps=false;
          vueObj.UnProductiveEployees.forEach((item, index) => {
            var colors ="";
            if (index==0)
                colors="#ca44b0";
            else if (index==1)
                colors="#c1804f";
            else if (index==2)
                colors="#1facf2";
            else if (index==3)
                colors="#2c7be5";
            else if (index==4)
                colors="#70a5ed";
            else
            colors="#2dbd2d";
            var objProgressOption = {
              text: {
                color: "#FFFFFF",
                shadowEnable: true,
                shadowColor: "#000000",
                fontSize: 12,
                fontFamily: "Helvetica",
                dynamicPosition: false,
                hideText: true,
              },
              progress: {
                color: colors,
                backgroundColor: "#F2F2F2",
                inverted: false,
              },
              layout: {
                height: 8,
                width: 100,
                verticalTextAlign: 61,
                horizontalTextAlign: 43,
                zeroOffset: 0,
                strokeWidth: 30,
                progressPadding: 0,
                type: "line",
              },
            };
            vueObj.UnProductiveEployees[index].progress_bar_options = objProgressOption;
         
          });
          vueObj.TempProductiveApps = [];
          vueObj.TempProductiveUnemp= [];
          if( vueObj.ProductiveApps){
            vueObj.ProductiveApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveApps.push(item);
            }
          });
          }
          if (vueObj.UnProductiveEployees){
            vueObj.UnProductiveEployees.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveUnemp.push(item);
            }
          });
          }
          vueObj.isMostUnProductiveLoading=false;
        }
        else{
          vueObj.isMostUnProductiveLoading=false;
        }
      });
    },

    MyViewPage(ths) {
      this.checkedUserId = ths;
      // this.DashBoardVue=true;
      this.$router.push({ name: "Timetrackid", params: { id: ths }, });

    },
    GetMostUsedApps: async function () {
      var vueObj = this;
      vueObj.UnProductiveApps=false;
      vueObj.ProductiveLoader= false;
      vueObj.chartOptions.xaxis.categories = [];
      vueObj.series1[0].data = [];
      vueObj.series1[1].data = [];
      vueObj.series1[2].data = [];
      var param = "";
      vueObj.mostuserappdata = false;
      if ((vueObj, dateType == "DD")) {
        param = `fromDate=${vueObj.fromDate}&toDate=${vueObj.ToDate}&type=${vueObj.dateType}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&viewType=DepartmentView`;
      } else if (vueObj.dateType == "WK") {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.startDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.endDate)}&type=${vueObj.dateType}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&viewType=DepartmentView`;
      } else {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.monthStartDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.monthEndDate)}&type=${vueObj.dateType}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&viewType=DepartmentView`;
      }
      await DataService.GetMostUsedApps(param).then((response) => {
        vueObj.mostusedAppsData = response.data.data;
        if (vueObj.mostusedAppsData != null) {
          vueObj.mostusedAppsData.forEach((item, index) => {
            vueObj.chartOptions.xaxis.categories.push(
              item.application_display_name
            );
            if(item.productive_type =="Productive") {
              vueObj.series1[0].data.push(item.percentage_duration);
            }
            else if (item.productive_type =="Unproductive"){
              vueObj.series1[1].data.push(item.percentage_duration);
            }
            else {
              vueObj.series1[2].data.push(item.percentage_duration);
            }
            vueObj.mostuserappdata = true;
          });
        } else {
          vueObj.mostusedAppsData = [];
        }
      });
    },
    GetUsedAppTimeDurationByCategories: async function () {
      var vueObj = this;
      var param = "";
      param = `fromDate=${vueObj.fromDate}&toDate=${vueObj.ToDate}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&type=${vueObj.dateType}&viewType=DepartmentView`;
      if ((vueObj, dateType == "DD")) {
      } else if (vueObj.dateType == "WK") {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.startDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.endDate)}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&type=${vueObj.dateType}&viewType=DepartmentView`;
      } else {
        param = `fromDate=${vueObj.ConvertDateFormat(vueObj.monthStartDate)}&toDate=${vueObj.ConvertDateFormat(vueObj.monthEndDate)}&userIds=${vueObj.childerId == undefined ? '' : vueObj.childerId}&type=${vueObj.dateType}&viewType=DepartmentView`;
      }
      await DataService.GetUsedAppTimeDurationByCategories(param).then((response) => {
        vueObj.AppDurationByCategories = response.data.data;
        if ( vueObj.AppDurationByCategories != null) {
          vueObj.AppDurationByCategories.forEach((item, index) => {
            var objProgressOption = {
              text: {
                color: "#FFFFFF",
                shadowEnable: true,
                shadowColor: "#000000",
                fontSize: 12,
                fontFamily: "Helvetica",
                dynamicPosition: false,
                hideText: true,
              },
              progress: {
                color: item.color_code,
                backgroundColor: "#F2F2F2",
                inverted: false,
                width: item.percentage_duration,
              },
              layout: {
                height: 10,
                width: item.percentage_duration,
                verticalTextAlign: 61,
                horizontalTextAlign: 43,
                zeroOffset: 0,
                strokeWidth: 30,
                progressPadding: 0,
                type: "line",
              },
            };
            vueObj.AppDurationByCategories[index].progress_bar_options = objProgressOption;

          });
          vueObj.TempProductiveApps = [];
          if( vueObj.ProductiveApps){
          vueObj.ProductiveApps.forEach((item, index) => {
            if (index < 5) {
              vueObj.TempProductiveApps.push(item);
            }
          });
          }
        }
      }
      );
    },
  },
}
